@charset "UTF-8";
@font-face {
  font-family: "Corbel";
  src: url("../../fonts/Corbel-reg.woff") format("woff2"), url("../../fonts/Corbel-reg.woff") format("woff");
}
* {
  box-sizing: border-box;
  vertical-align: top;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  outline: none;
  border: none;
  transition: all 0.2s;
  font-family: 'PT Sans', sans-serif;
}
body {
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}
.title_2 {
  font: 28px/32px 'PT Sans';
  color: #102673;
  font-weight: 400;
  text-transform: uppercase;
}
.header-top {
  padding: 8px 0 5px 0;
  background: #0f121f;
  position: relative;
}
.header-top .tel {
  font: 18px/24px 'PT Sans';
  font-weight: 400;
}
.header-top .mail {
  font: 16px/24px 'PT Sans';
  font-weight: 400;
}
.header-top .pop {
  background: #102a83;
  color: #fff;
  font: 16px/16px 'PT Sans';
  width: 100%;
  padding: 12px 10px;
  font-weight: 400;
}
.header-bottom {
  position: sticky;
  top: 0;
  left: 0;
  background: transparent;
  margin: 0 0 -52px 0;
}
.header-bottom .navbar {
  padding: 9px 0;
  background: #ebebeb;
  width: 100%;
  justify-content: space-between;
}
.header-bottom .nav-item {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}
.header-bottom .nav-link {
  text-transform: uppercase;
  font: 18px/22px 'PT Sans';
  color: #0f121f;
  font-weight: 400;
  display: flex;
  align-items: center;
  padding: 6px;
}
.header-bottom .show .nav-link {
  background: transparent;
  color: #0f121f;
}
.header-bottom .show .nav-link svg {
  transform: rotate(180deg);
}
.header-bottom .dropdown-menu {
  margin: 0;
  top: 44px;
  border: none;
  border-radius: inherit;
  width: max-content;
  background: #000;
}
.header-bottom .dropdown-item {
  font: 16px/22px 'PT Sans';
  color: #1e1e1e;
  font-weight: 400;
}
.page-index .section-slider {
  background: #0f121f url("../../images/slider-bg.jpg") no-repeat center bottom;
  background-size: cover;
  min-height: 480px;
  display: flex;
  align-items: center;
}
.page-index .section-slider .content {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page-index .section-slider .title {
  font: 32px/32px 'PT Sans';
  color: #fff;
  margin: 0 0 20px 0;
}
.page-index .section-slider .title span {
  display: block;
  font: 28px/32px 'PT Sans';
}
.page-index .section-slider .text {
  font: 18px/24px 'PT Sans';
  color: #fff;
  margin: 0 0 25px 0;
}
.page-index .section-slider .button {
  background: #102a83;
  color: #fff;
  font: 16px/16px 'PT Sans';
  padding: 12px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  margin: 0 auto;
}
.page-index .section-slider .slick-prev,
.page-index .section-slider .slick-next {
  top: auto;
  bottom: -66px;
  left: 50%;
  height: 30px;
  width: 20px;
}
.page-index .section-slider .slick-prev:before,
.page-index .section-slider .slick-next:before {
  content: url("../../images/chevron.svg");
}
.page-index .section-slider .slick-prev {
  transform: translateX(-35px) rotate(180deg);
}
.page-index .section-slider .slick-next {
  transform: translateX(15px);
}
.page-index .section-product {
  padding: 54px 0;
}
.page-index .section-product .product {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -5px;
}
.page-index .section-product .product-one {
  margin: 0 5px 20px 0;
  text-decoration: none;
  width: 19%;
}
.page-index .section-product .product-one .title {
  font: 18px/18px 'PT Sans';
  color: #0f121f;
  font-weight: 600;
  margin: 0 0 20px 0;
}
.page-index .section-product .product-one .img {
  position: relative;
  width: 100%;
  height: 229px;
  border: 1px solid #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
}
.page-index .section-product .product-one .img img {
  object-fit: contain;
}
.page-index .section-product .product-one .img:before {
  display: block;
  position: absolute;
  transform: translateX(-50%);
  top: -1px;
  left: 50%;
  content: "";
  width: 98px;
  height: 6px;
  background: #102a83;
}
.page-index .section-service .service {
  padding: 20px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -5px;
}
.page-index .section-service .service-one {
  text-decoration: none;
  text-align: center;
  margin: 0 0 20px 0;
}
.page-index .section-service .service-one .title {
  font: 18px/18px 'PT Sans';
  color: #0f121f;
  font-weight: 600;
  margin: 0 0 20px 0;
}
.page-index .section-service .service-one .img {
  position: relative;
  width: 100%;
  height: 229px;
  border: 1px solid #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
}
.page-index .section-service .service-one .img img {
  width: 100%;
  height: 100%;
}
.page-index .section-service .service-one .img:before {
  display: block;
  position: absolute;
  transform: translateX(-50%);
  top: -1px;
  left: 50%;
  content: "";
  width: 98px;
  height: 6px;
  background: #102a83;
}
.page-index .section-advanced {
  background: url("../../images/prey-bg.png") no-repeat center;
  padding: 37px 0 90px 0;
}
.page-index .section-advanced .wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 0 20px 0;
}
.page-index .section-advanced .text {
  font: 20px/22px 'PT Sans';
  font-weight: 600;
  color: #0f121f;
}
.page-index .section-form {
  background: #0f121f url("../../images/form-bg.jpg") no-repeat center top;
  background-size: cover;
  display: flex;
  align-items: center;
  padding: 57px 0;
}
.page-index .section-form .label,
.page-index .section-form .input {
  width: 100%;
  background: #fff;
  padding: 8px;
}
.page-index .section-form .label::placeholder,
.page-index .section-form .input::placeholder {
  color: #0f121f;
  font: 16px/22px 'PT Sans';
  font-weight: 400;
}
.page-index .section-form .input {
  border: 1px solid #dddddd;
}
.page-index .section-form .textarea {
  height: 125px;
}
.page-index .section-form .text {
  color: #fff;
  font: 14px/18px 'PT Sans';
  font-weight: 400;
  margin: 0;
  text-align: end;
}
.page-index .section-form .form_btn {
  background: #102a83;
  color: #fff;
  font: 16px/16px 'PT Sans';
  width: 100%;
  padding: 24px 10px;
  font-weight: 400;
}
.page-index .section-about {
  padding: 44px 0;
  margin: 0 0 50px 0;
}
.page-index .section-about .block {
  display: flex;
  align-items: center;
  position: relative;
}
.page-index .section-about .block .img {
  position: absolute;
  z-index: -1;
}
.page-index .section-about .block-text {
  padding: 22px 22px 22px 400px;
  margin: 0 0 0 40px;
  box-shadow: 0 0 27px rgba(1, 1, 2, 0.1);
}
.page-index .section-about .block-text .text {
  font: 16px/18px 'PT Sans';
  font-weight: 600;
  color: #0f121f;
}
.page-index .section-partners {
  padding: 56px 0 0 0;
  background: url("../../images/partner-bg.jpg") no-repeat;
  background-size: cover;
}
.page-index .section-partners .partners-slick {
  width: 100%;
}
.page-index .section-partners .partners-slick .slick-track {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.page-index .section-partners .partners-slick .slick-slide {
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-index .section-partners .partners-slick .slick-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 10px;
}
.page-index .section-partners .partners-slick .img {
  margin: 0 0 30px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.page-index .section-partners .partners-slick img {
  width: 100%;
}
.page-index .section-partners .slick-prev,
.page-index .section-partners .slick-next {
  top: -80px;
  background: #102673;
  height: 40px;
  width: 40px;
}
.page-index .section-partners .slick-prev:before,
.page-index .section-partners .slick-next:before {
  display: block;
  content: url("../../images/chevron-partner.svg");
}
.page-index .section-partners .slick-prev {
  left: 0;
}
.page-index .section-partners .slick-prev:before {
  transform: rotate(180deg);
}
.page-index .section-partners .slick-next {
  right: 0;
}
.page-index .section-info {
  padding: 56px 0 22px 0;
}
.page-index .section-info .btn_all {
  background: #102a83;
  color: #fff;
  font: 16px/16px 'PT Sans';
  display: block;
  text-align: center;
  padding: 12px 20px;
  font-weight: 400;
}
.page-index .section-info .news {
  text-decoration: none;
  margin: 0 0 30px 0;
  background: #f8f8f8;
  display: flex;
  justify-content: center;
  position: relative;
  max-height: 200px;
}
.page-index .section-info .news:before {
  display: block;
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  border-style: solid;
  border-width: 6.3rem 26px 6.3rem 2px;
  border-color: #fff transparent #fff #fff;
  line-height: 0;
}
.page-index .section-info .news img {
  width: 225px;
  height: 100%;
  object-fit: cover;
}
.page-index .section-info .news .text {
  border: 1px solid #f8f8f8;
  background: #f8f8f8;
  padding: 20px;
}
.page-index .section-info .news .text-content {
  color: #0f0f0f;
  font: 16px/18px 'PT Sans';
  font-weight: 400;
}
.page-index .section-info .news .date {
  color: #9e9e9e;
  font: 16px/16px 'PT Sans';
  font-weight: 400;
  margin: 0 0 10px 0;
}
.page-index .section-info .news .title {
  color: #0f0f0f;
  font: 18px/22px 'PT Sans';
  font-weight: 400;
}
.page-index .section-info .news .link {
  color: #102673;
  font: 18px/22px 'PT Sans';
  font-weight: 400;
}
.footer {
  background: #0f121f;
  padding: 20px 0;
}
.footer * {
  color: #fff;
  text-decoration: none !important;
}
.footer .title {
  font: 18px/26px 'PT Sans';
  font-weight: 400;
  text-transform: uppercase;
}
.footer .list li:first-child a {
  font-weight: 700;
}
.footer a:hover {
  color: #374996;
}
.footer a:hover * {
  color: #374996;
}
.footer .cor {
  font: 18px/18px 'Corbel';
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-prev:hover,
.slick-next:hover,
.slick-prev:focus,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-prev {
  left: -25px;
}
[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}
.slick-prev:before {
  content: "←";
}
[dir="rtl"] .slick-prev:before {
  content: "→";
}
.slick-next {
  right: -25px;
}
[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}
.slick-next:before {
  content: "→";
}
[dir="rtl"] .slick-next:before {
  content: "←";
}
/* Dots */
.slick-dotted .slick-slider {
  margin-bottom: 30px;
}
.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0;
  font-size: 0;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}
